import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {Bread} from '../models/Bread';

@Injectable()
export class BreadsService {

  breads: Bread[] = [
    new Bread('לחם א 1', 'לחם מקמח שיפון 100% בתוספת חמוציות ואגוזים', 25, 'https://static1.squarespace.com/static/56fd379ad51cd41823eb00a1/56fecd6ab09f95b37d28b8d0/571b6ccc1bbee027950a0ceb/1461415118703/boulangerie-jade-products-breads3.jpg?format=1500w'),
    new Bread('לחם א 2', 'לחם מקמח קוסמין 100% ממחמצת טרייה', 15, 'https://fastfoodinusa.com/templates/uploads/gallery/big/Panera_Bread_Rye_Loaf.jpg'),
    new Bread('לחם א 3', 'לחם 50% מלא 50% לבן למושחתים שבינינו', 17, 'http://www.brezelbar.com.au/wp-content/uploads/2017/10/rye-roll-small.jpg'),
    new Bread('לחם א 4', 'לחם 100% קמח מלא בתוספת עגבניות מיובשות ופלפלים קלויים', 17, 'http://www.recipe4living.com/assets/itemimages/400/400/3/default_590913972ffb4361bcbdd181773eb493_Depositphotos_13255822_m.jpg'),
  ];

  constructor(private http: HttpClient) {
  }

  getAllBreads(): Observable<Bread[]> {
    return Observable.create((observe) => {
      observe.next(this.breads);
      observe.complete();
    });
  }

  addNewBread(bread:Bread){
    // this.http.post()
  }
}



