import {Component, OnInit} from '@angular/core';
import {BreadsService} from '../../services/breads.service';
import {Bread} from '../../models/Bread';

@Component({
  selector: 'app-bread-order',
  templateUrl: './bread-order.component.html',
  styleUrls: ['./bread-order.component.css']
})
export class BreadOrderComponent implements OnInit {

  breads: Bread[] = [];

  constructor(private breadService: BreadsService) {
  }

  ngOnInit() {
this.getAllBreads();
  }

  getAllBreads() {
    this.breadService.getAllBreads().subscribe(
      (breads) => {
        this.breads = breads;
        console.log(this.breads);
      },
      (error) => {
        console.error(error);
      }
    );
  }
}



