import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HttpClientModule} from '@angular/common/http';
import {AgmCoreModule} from '@agm/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {
  MatToolbarModule,
  MatCheckboxModule,
  MatGridListModule,
  MatCardModule,
  MatTabsModule,
  MatIconModule,
  MatFormFieldModule,
  MatOptionModule,
  MatInputModule,
  MatButtonModule,
  MatTableModule
} from '@angular/material';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {AppComponent} from './app.component';
import {CarouselComponent} from './components/home-related/carousel/carousel.component';
import {MenuComponent} from './components/shared/menu/menu.component';
import {SentencesComponent} from './components/home-related/sentences/sentences.component';
import {HomeAltComponent} from './components/home-related/home/home-alt.component';
import {LogoComponent} from './components/shared/logo/logo.component';
import {MainComponentComponent} from './components/main-component/main-component.component';
import {BreadOrderComponent} from './components/bread-order/bread-order.component';
import {BreadsService} from './services/breads.service';
import {BreadCardComponent} from './components/bread-order/bread-card/bread-card.component';
import {AboutComponent} from './components/about/about.component';
import {NotFoundComponent} from './components/shared/not-found/not-found.component';
import {DirectionsComponent} from './components/directions/directions.component';
import {MainComponent} from './components/managment/main/main.component';
import {TabsMenuComponent} from './components/managment/tabs-menu/tabs-menu.component';
import {AddProductComponent} from './components/managment/add-product/add-product.component';
import {EditProductComponent} from './components/managment/edit-product/edit-product.component';
import {OrdersComponent} from './components/managment/orders/orders.component';

const routes = [
  {path: '', redirectTo: '/home', pathMatch: 'full'},
  {path: 'home', component: HomeAltComponent},
  {path: 'bread-order', component: BreadOrderComponent},
  {path: 'about', component: AboutComponent},
  {path: 'directions', component: DirectionsComponent},
  {
    path: 'management', component: MainComponent,
    children: [
      {path: 'add-product', component: AddProductComponent},
      {path: 'edit-product', component: EditProductComponent},
      {path: 'orders', component: OrdersComponent},
    ]
  },
  {path: 'not-found', component: NotFoundComponent},
  {path: '*', redirectTo: '/not-found'},

];

@NgModule({
  declarations: [
    AppComponent,
    CarouselComponent,
    MenuComponent,
    SentencesComponent,
    HomeAltComponent,
    LogoComponent,
    MainComponentComponent,
    BreadOrderComponent,
    BreadCardComponent,
    AboutComponent,
    NotFoundComponent,
    DirectionsComponent,
    MainComponent,
    TabsMenuComponent,
    AddProductComponent,
    EditProductComponent,
    OrdersComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(routes),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyA0bxjxKwQjIHM0QvFy5snAQzYnAOX2vUg'
    }),
    BrowserAnimationsModule,
    MatToolbarModule,
    MatCheckboxModule,
    MatGridListModule,
    MatCardModule, MatTabsModule, MatIconModule, MatFormFieldModule, MatOptionModule, MatInputModule, MatButtonModule, MatCardModule, MatTableModule,

    FormsModule, ReactiveFormsModule
  ],
  providers: [BreadsService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



